 

body h2, p{
  margin:0;
  padding:0;


}
  
  

body{
  padding:3em;
  background: fixed;
  background-image: url(./images/bg-desktop.svg); background-color: rgb(67, 35, 128);
  justify-content: center;
  align-items: center;
  background-repeat: no-repeat;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400px;
  color:rgb(244, 242, 247);;
}


header img{
  width: 10%;
}
main {
  margin-top: 2em;
  display: flex;
  justify-content: space-between;


}
main img{
  width: 55%;
  
}
.content {
  width:45%;
  padding-top:2em;
  margin-left: 10%;
  
  
}
.content h2{
  color:rgb(244, 242, 247);
  font-weight: 600px;
  font-family: 'Poppins', sans-serif;

}
.content p{
  margin:2em 0em;
  font-size:15px;
  font-family: 'Poppins', sans-serif;
  font-weight: 400px;
  color:rgb(244, 242, 247);
  
}
main button{
  padding:1em;
  border:none;
  border-radius: 30px;
  color: rgb(67, 35, 128);
  width: 40%;

}
footer{
 
  display: flex;
  justify-content:space-between;
   
  
 
}

button {
  border-radius: 100%;
  border-bottom: white;  
  color: white;
  text-align: center;
  width: 50px;
  height: 50px;
  line-height: 10px;
  text-align: center;
  font-size: 20px;
  text-decoration: none;

  
  
}
.fa-facebook-f {
 padding: 1em;
 border-color: white;
 border-width: 5px;
 border-radius: 100%;
 border-style: solid;
 border-width: thin;
 text-align: center;
 width: 1%;
 height: 1%;
 align-items: center;
 margin-left: 85%;
 color:white;
 
 

}
 .fa-twitter {
  padding: 1em;
  border-color: white;
  border-width: 10px;
  border-radius: 100%;
  border-style: solid;
  border-width: thin;
  text-align: center;
  width: 1%;
  height: 1%;
  align-items: center;
  margin-left: 5px;
  color:white;
}
.fa-instagram-square {
  padding: 1em;
  border-color: white;
  border-width: 10px;
  border-radius: 100%;
  border-style: solid;
  border-width: thin;
  text-align: center;
  width: 1%;
  height: 1%;
  align-items: center;
  margin-left: 5px;
  color:white;
} 


@media  (max-width: 500px) {
 body {
   background-color: aqua;

   }
 }